@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu';
}

.right {
  background-color: #fff;
  color: #005b96;
  height: 100vh;
  width: 50%;
  overflow: auto;
  position: fixed;
  right: 0;
}

.App {
  text-align: center;
  font-family: 'Ubuntu', sans-serif;
}

.Appdown {
  text-align: center;
  font-family: 'Ubuntu', sans-serif;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

::-webkit-scrollbar {
  width: 0px;  /* remove scrollbar space */
  height: 0px;
  background: transparent;  /* optional: just make scrollbar invisible */
}
/* optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #FF0000;
}

.intro-paragraph {
  font-size: 14px;
  width: 500px;
  margin: 0 auto;
  padding: 10px;
  line-height: 20px;
}

@media (max-width: 1050px) {
  .intro-paragraph {
    width: calc(50vw - 50px);
    margin: none;
  }
}

.circle-slider {
  height: 40px;
  width: 40px;
  position: fixed;
  left: calc(50% - 23.5px);
  border-radius: 50%;
  background-color: #005b96;
  top: 50%;
  border-style: solid;
  border-color: #fff;
  cursor: move;
  z-index: 20;
}

.circle-inner {
  font-size: 20px;
  color: #fff;
  height: 100%;
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.contact-paragraph {
  font-size: 14px;
  width: 500px;
  margin: 0 auto;
}

.contact-container {
  padding-top: 10px;
  padding-bottom: 20px;
}

.flexicution {
  display: flex;
}

.header-james {
  width: 20%;
  position: fixed;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding-top: 10px;
  font-size: 18px;
  color: #000;
  z-index: 100;
  font-weight: bold;
}

.header-align {
  /*margin: 0 auto;*/
  font-size: 18px;
  padding: 10px;
}

.header-padding {
  height: 40px;
  width: 100%;
}

.header-topic {
  font-size: 20px;
  padding: 10px;
}

.header-container {
  height: 40px;
  position: fixed;
  z-index: 10000;
}

.header-items {
  padding: 10px 10px 0 10px;
  cursor: pointer;
  font-size: 16px;
}

.header-left {
  float: left;
}

.header-left:hover {
  color: #D3D3D3;
}

.header-right {
  float: right;
}

.header-right:hover {
  color: rgb(0, 137, 229);
}

.jazz-container {

}

.top-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px 50px;
}

.bottom-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px 50px;
}

.jazz-pict {
  margin: 0 auto;
}

.jazz-description {
  width: 350px;
  margin: 0 auto;
}

.header {
  font-size: 18px;
  padding: 10px;
}

.audio-title {
  font-size: 14px;
  padding: 10px;
}

.left {
  background-color: #005b96;
  color: #fff;
  height: 100vh;
  width: 50%;
  overflow: auto;
  position: fixed;
}

.all-projects {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.project-container {
  width: 200px;
  height: 200px;
  padding: 10px 50px;
  margin: 0 auto;
  z-index: 200;
}

.project-tile {
  position: relative;
}
.project-text {
  position: absolute;
  z-index: 100;
  display: none;
  width: 180px;
  text-align: left;
  padding: 10px;
  font-family: 'Ubuntu';
  text-decoration: none;
  color: #fff;
}

.project-header {
  font-size: 16px;
}

.project-description {
  padding-top: 10px;
  font-size: 14px;
}

.project-technologies {
  padding-top: 10px;
  font-size: 14px;
}

.project-container:hover .project-text{
  display: block;
}

.project-container:hover .project-image{
  opacity: 0.2;
}

.project-image {
  height: 100%;
  width: 100%;
  float: none;
  z-index: 50;
}

.project-image:hover{
  opacity: 0.2;
}

